#infoArea {
  place-self: start end;
}

#infoArea {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-top: 2px solid #aaa;
  border-right: 2px solid #aaa;
}

#infoArea table, #infoArea thead, #infoArea tbody, #infoArea tr {
  display: contents;
}

#infoArea td, #infoArea th {
  padding: 8px;
  border-left: 2px solid #aaa;
  border-bottom: 2px solid #aaa;
}

#infoArea b {
  font: normal 150% monospace;
  text-transform: uppercase;
}

.boolText {
  text-shadow: 0px 0px 2px white;
  text-transform: uppercase;
}

.board {
  place-self: start center;
  display: grid;
  grid-template: repeat(8, 50px) / repeat(8, 50px);
  font-size: x-small;
}

.boardTile {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: monospace;
  user-select: none;
}

.controls {
  margin: auto;
}

.controls button, .controls select, .controls label {
  font-size: inherit;
  margin: 5px;
}

.historyBoard {
  display: inline-grid;
  background: center / 100% 100% url("./board_background.png");
  padding: 13px 13px 10.5px 13.5px;
  margin: 8px;
  font-size: large;
}

#settings {
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  margin: auto;
}

#settings > div {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: 1fr;
  gap: 2px;
  justify-items: center;
}

/* This rule (and class set by javascript) are not ideal. I can't figure out a
 * way to get this effect without them.
 */
#settings.simple > div {
  grid-template-columns: repeat(6, 1fr);
}

#settings legend {
  margin: auto;
}

#settings input {
  width: 6ch;
}

summary {
  margin: 10px auto 5px;
  cursor: pointer;
  user-select: none;
}

#sequenceConfig {
  margin: auto;
  width: fit-content;
  border-radius: 8px;
}

#sequenceConfig legend {
  margin: auto;
}

#sequenceConfig div {
  text-align: left;
}
